import {
    Component,
    EventEmitter,
    ChangeDetectionStrategy,
    Output,
    Input,
    ViewEncapsulation,
    OnInit,
    TemplateRef
} from '@angular/core';
import { CdkDragDrop, moveItemInArray, transferArrayItem } from '@angular/cdk/drag-drop';
import { KanbanBoard, KanbanColumn, KanbanItem } from './cmacs-kanban-definitions';
import { InputBoolean } from 'ng-zorro-antd';

@Component({
    selector: 'cmacs-kanban',
    exportAs: 'cmacsKanban',
    templateUrl: './cmacs-kanban.component.html',
    styleUrls: ['./cmacs-kanban.component.css'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None
})

export class CmacsKanbanComponent implements OnInit {

    /** Items to display  */
    @Input() board: KanbanBoard;

    /** Template for items to render. "item" object ist passed (see examples) */
    @Input() itemTemplate: TemplateRef<any> = null;

    /** Template for column headers. Current groupName will be passed (see examples) */
    @Input() columnHeaderTemplate: TemplateRef<any> = null;

    // scrolling
    @Input() @InputBoolean() hasVerticalScroll = false;
    @Input() heightContainer = '500px';

    // styles
    @Input() columnWidth: string;

    @Output() draggedItem = new EventEmitter<object>();
    @Output() onclickItem = new EventEmitter<object>();
    @Output() ondblclickItem = new EventEmitter<object>();
    @Output() selectionChange = new EventEmitter<KanbanItem[]>();

    selectedItems: KanbanItem [] = [];

    constructor() { }

    ngOnInit(): void {

    }

    verticalScrollStyle(): any {
        return (this.hasVerticalScroll) ? { height: this.heightContainer} : {};
    }

    columnStyle(): any {
        return (this.columnWidth) ? { minWidth: this.columnWidth} : {};
    }

    drop(event: CdkDragDrop<string[]>) {
        this.draggedItem.emit(event.item.data);
        if (event.previousContainer === event.container) {
            moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
        } else {
            transferArrayItem(event.previousContainer.data,
                event.container.data,
                event.previousIndex,
                event.currentIndex);
        }
    }

    clickItem(item: KanbanItem) {
        this.onclickItem.emit(item);

        // add or remove elements to selected items
        if (!item.disabled) {
            const idx = this.selectedItems.findIndex(elem => elem.id === item.id);
            if (idx === -1) {
                this.selectedItems.push(item);
            } else {
                this.selectedItems.splice(idx, 1);
            }
            this.selectionChange.emit(this.selectedItems);
        }
    }

    dblclickItem(item: object) {
        this.ondblclickItem.emit(item);
    }

    isItemSelected(id: string) {
        return this.selectedItems.some(elem => elem.id === id);
    }
}
